import React from 'react';
import { affiliateProducts } from '../data/affiliateProducts';
import { ProductCard } from './ProductCard';
import { ToolId } from './utils/slugs';

interface BakingEbooksPageProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const BakingEbooksPage: React.FC<BakingEbooksPageProps> = ({ setActiveTool, breadcrumbs }) => {
    const ebooks = affiliateProducts.filter(p => p.category === 'Ebooks');

    const handleNavigate = (path: string) => {
        // This is a placeholder as ProductCard expects a handleNavigate function
        // In a real app, this might use the router, but here we just need to satisfy the interface
        window.location.href = path;
    };

    return (
        <div className="min-h-screen bg-brand-background">
            {/* Hero Section */}
            <div className="bg-gradient-to-b from-brand-pink-light to-brand-background pt-8 pb-12 px-4 sm:px-6 lg:px-8 rounded-b-3xl mb-12">
                <div className="max-w-4xl mx-auto text-center">
                    <div className="mb-6 flex justify-center">{breadcrumbs}</div>
                    <h1 className="text-4xl md:text-5xl font-serif font-bold text-brand-text-title mb-4">
                        Popular Baking Ebooks
                    </h1>
                    <p className="text-lg text-brand-text-body mb-8 max-w-2xl mx-auto">
                        Explore our curated collection of the best baking ebooks. From sourdough mastery to cookie perfection, find your next favorite cookbook here.
                    </p>
                </div>
            </div>

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pb-20">
                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
                    {ebooks.map((product) => (
                        <ProductCard key={product.id} product={product} handleNavigate={handleNavigate} />
                    ))}
                </div>
            </div>
        </div>
    );
};
